import { getQuery } from 'api/queries/query';
import React from 'react';
import { translate } from 'react-i18next';
import { connect } from 'react-redux';
import { createMapStateToProps } from 'store/common';
import { reportActions, reportSelectors } from 'store/reports';
const reportType = "tag" /* tag */;
class TagViewBase extends React.Component {
    constructor() {
        super(...arguments);
        this.getTags = () => {
            const { report } = this.props;
            const tags = [];
            if (report) {
                for (const tag of report.data) {
                    for (const val of tag.values) {
                        tags.push(`${tag.key}: ${val}`);
                    }
                }
            }
            return tags;
        };
    }
    componentDidMount() {
        const { fetchReport, queryString, reportPathsType } = this.props;
        fetchReport(reportPathsType, reportType, queryString);
    }
    componentDidUpdate(prevProps) {
        const { fetchReport, queryString, reportPathsType } = this.props;
        if (prevProps.queryString !== queryString) {
            fetchReport(reportPathsType, reportType, queryString);
        }
    }
    render() {
        const tags = this.getTags();
        return tags.map((tag, index) => React.createElement("div", { key: `tag-${index}` }, tag));
    }
}
const mapStateToProps = createMapStateToProps((state, { filterBy, groupBy, reportPathsType }) => {
    const queryString = getQuery({
        filter: {
            [groupBy]: filterBy,
            resolution: 'monthly',
            time_scope_units: 'month',
            time_scope_value: -1,
        },
    });
    const report = reportSelectors.selectReport(state, reportPathsType, reportType, queryString);
    const reportFetchStatus = reportSelectors.selectReportFetchStatus(state, reportPathsType, reportType, queryString);
    return {
        queryString,
        report,
        reportFetchStatus,
    };
});
const mapDispatchToProps = {
    fetchReport: reportActions.fetchReport,
};
const TagView = translate()(connect(mapStateToProps, mapDispatchToProps)(TagViewBase));
export { TagView };
